.mainHeader {
  width: 100vw;
  height: calc(100vh - 3rem);
  display: grid;
  place-items: center;
  margin-top: 3rem;
}

.mainHeaderContainer {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  gap: 6rem;
}

.mainHeaderLeft {
  margin-top: -3rem;
}

.mainHeaderLeft h4 {
  margin-bottom: 1rem;
  color: var(--color-secondary);
  font-weight: 400;
}

.mainHeaderLeft h1 {
  font-size: 2.5rem;
}

.mainHeaderLeft p {
  margin: 1rem 0 2.5rem;
  font-size: 1.1rem;
  line-height: 1.5rem;
}

.mainHeaderRight {
  display: grid;
  place-items: center;
  position: relative;
}

.mainHeaderCircle {
  width: 25rem;
  height: 25rem;
  background: linear-gradient(75deg, var(--color-secondary), transparent);
  position: absolute;
  border-radius: 50%;
  transition: var(--transition);
  filter: blur(15px);
}


.mainHeaderImage {
  position: relative;
}

/* ================ Programs ================*/
.programs {
  margin-top: 4rem;
}

.programsWrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 3rem;
  margin-top: 4rem;
}

.programsProgram a {
  margin-top: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.programsProgram:hover a {
  color: var(--color-gray-600);
  background: var(--color-secondary);
}
/* ================ Values ================*/
.valuesContainer {
  display: grid;
  grid-template-columns: 38% 50%;
  gap: 12%;
}

.valuesImage {
  filter: saturate(0.25);
  transition: var(--transition);
}

.valuesImage:hover {
  filter: saturate(1);
}

.valuesRight > p {
  margin: 1.5rem 0 5rem;
}

.valuesWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3.4rem 2.4rem;
}

.card.valuesValue {
  padding-top: 3.5rem;
  text-align: left;
  position: relative;
}

.card.valuesValue span {
  position: absolute;
  top: -1.5rem;
}

/* ================ FAQS ================*/

.faqsWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem 5rem;
  align-items: flex-start;
  margin-top: 4rem;
}

.faq {
  background: var(--color-gray-500);
  padding: 2rem;
  border-radius: 1rem;
  cursor: pointer;
}

.faq div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.faqIcon {
  background: transparent;
  color: var(--color-gray-100);
  font-size: 1.5rem;
}

.faq p {
  margin-top: 1.5rem;
}

/* ================ Testimonials ================*/
.testimonials {
  width: 50rem;
  margin-inline: auto;
}

.testimonialsHead {
  justify-content: center;
}

.card.testimonial {
  position: relative;
  text-align: left;
  margin-top: 5rem;
}

.testimonialAvatar {
  width: 4rem;
  height: 4rem;
  border-radius: 1.5rem;
  overflow: hidden;
  position: absolute;
  top: -2rem;
  left: calc(50% - 2rem);
  box-shadow: 0 1.5rem 2rem rgba (26, 26, 54, 0.4);
  transition: var(--transition);
  border: 2px solid transparent;
}

.testimonial:hover .testimonialAvatar {
  border-radius: 50%;
  border-color: var(--color-gray-400);
}

.testimonialQuote {
  font-style: italic;
  margin: 1rem 0 2rem;
}

.card small.testimonialTitle {
  margin-top: 0.3rem;
}

.testimonialsBtnContainer {
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: 2.5rem auto 0rem;
}

.testimonialsBtn {
  background: transparent;
  cursor: pointer;
}

.testimonialsBtn svg {
  fill: var(--color-primary);
  font-size: 1.8rem;
  width: 3rem;
  height: 3rem;
}

/* ================ Footer ================*/
footer
{
  background: var(--color-primary);
  margin-top: 7rem;
  padding-top: 7rem;
  font-size: 0.9rem;
  color: var(--color-gray-100);
}

.footerContainer
{
  display: grid;
  grid-template-columns: 26rem 1fr 1fr 1fr;
  gap: 6rem; 
}

.footerContainer article
{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 0.7rem;
}

.footerContainer article p
{
  margin-top: 0.5rem;
}

.footerContainer article h4
{
  margin-bottom: 0.6rem;
  font-size: 1rem;
}

.footerSocials
{
  display: flex;
  align-items: center;
  margin-top: 1.5rem;
  gap: 1rem;
}

.footerSocials a
{
  display: inline-block;
  background: var(--color-gray-100);
  border: 1px solid transparent;
  border-radius: 0.6rem;
  transition: var(--transition);
  padding: 0.5rem 0.5rem 0.2rem;
}

.footerSocials a svg
{
  color: var(--color-gray-600);
}

.footerSocials a:hover
{
  border-color: var(--color-gray-100);
  background: transparent;
}

.footerCopyright
{
  color: var(--color-gray-100);
  text-align: center;
  padding: 1.5rem 0;
  margin-top: 5rem;
  border-top: 2px solid var(--color-primary-variants);
}

/* Media Queries  */
@media screen and (max-width: 1024px) {
  /* Main Header  */
  .mainHeader
  {
    height: fit-content;
    padding: 12rem 0;
  }
  
  .mainHeaderContainer
  {
    gap: 0;
  }

  .mainHeaderCircle
  {
    width: 16rem;
    height: 16rem;
  }

  /* Programs  */
  .programsWrapper{
    grid-template-columns: 1fr 1fr;
  }

  /* Values  */
  .valuesContainer
  {
    grid-template-columns: 1fr;
    justify-content: center;
    gap: 4rem;
  }

  .valuesWrapper
  {
    gap: 4rem 3rem;
  }

  .valuesImage
  {
    display: none;
  }

  /* FAQ  */
  .faqsWrapper
  {
    grid-template-columns: 1fr;
  }

  /* Testimonials */
  .testimonials
  {
    width: 75%;
  }

  /* Footer  */
  .footerContainer
  {
    grid-template-columns: 1fr 1fr;
    gap: 4rem;

  }
}

/* Media Quireis For Phones  */
@media screen and (max-width: 600px) 
{
  /* Main Header  */
  .mainHeader
  {
    margin-top: 0;
    padding: 12rem 0 0 ;
    height: 100vh;
  }

  .mainHeaderImage , .mainHeaderCircle
  {
    display: none;
  }

  .mainHeaderContainer
  {
    grid-template-columns: 1fr;
  }

  /* Programs  */
  .programsWrapper
  {
    grid-template-columns: 1fr;
    gap: 2rem;
    margin-top: 3rem;
  }

  .programsProgram{
    width: 84%;
    margin: 0 auto;
  }

  /* Values  */
  .valuesWrapper{
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }

  .card.valuesValue
  {
    width: 84%;
    margin-inline: auto;
  }

  /* FAQ  */
  .faqsWrapper
  {
    gap: 1rem;
    margin-top: 3rem;
  }

  /* Testimonials  */
  .testimonials
  {
    width: 100%;
  }

  /* Footer  */
  footer{
    margin-top: 3rem;
  }

  .footerContainer{
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .footerContainer article
  {
    align-items: center;
  }

  .footerContainer article p{
    text-align: center;
  }
}