nav {
  height: 5rem;
  width: 100vw;
  background: var(--color-primary);
  display: grid;
  place-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
}

/* only shows on medium and small screens */
.navToggleBtn {
  display: none;
}

.navContainer {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.logo {
  width: 7rem;
  display: block ;
}

.navLinks {
  display: flex;
  gap: 3.5rem;
  align-items: center;
}

.navLinks a {
  transition: var(--transition);
}

.navLinks a:hover {
  color: var(--color-secondary);
}

