/* Media Queries for medium and small screens */
@media screen and (max-width: 1024px) {
  .navToggleBtn {
    display: inline-block;
    background-color: transparent;
    font-size: 1.8rem;
    cursor: pointer;
  }

  .navToggleBtn svg {
    color: var(--color-gray-100);
  }

  .navLinks {
    position: absolute;
    top: 100%;
    right: 0;
    flex-direction: column;
    gap: 0;
    perspective: 400px;
  }
  /* 
  .activeNav,.activeNav::after{
    display: none;
  } */

  .navLinks li {
    height: 4rem;
    width: 100%;
    box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.4);
    transform: rotateX(90deg);
    opacity: 0;
    transform-origin: top;
    animation: navAnimation 600ms ease forwards;
  }

  .navLinks li a {
    background-color: var(--color-primary);
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 5rem /*1rem 3rem*/;
  }

  .showNav {
    display: flex;
  }

  .hideNav {
    display: none;
  }

  /* Animation of Nav Bar  */

  .navLinks li:nth-child(2) {
    animation-delay: 200ms;
  }
  .navLinks li:nth-child(3) {
    animation-delay: 400ms;
  }
  .navLinks li:nth-child(4) {
    animation-delay: 600ms;
  }
  .navLinks li:nth-child(5) {
    animation-delay: 800ms;
  }
  .navLinks li:nth-child(6) {
    animation-delay: 1s;
  }
  .navLinks li:nth-child(7) {
    animation-delay: 1.2s;
  }

  @keyframes navAnimation {
    to {
      transform: rotateX(0);
      opacity: 1;
    }
  }
}
