.contactContainer
{
    display: grid;
    place-items: center;
}

.contactWrapper
{
    width: fit-content;
    display: flex;
    gap: 2rem;
}
.contactWrapper a
{
    background: var(--color-primary);
    font-size: 2rem;
    border: 2px solid transparent;
    transition: var(--transition);
    padding: 2rem;
    border-radius: 1.5rem;
}

.contactWrapper a:hover
{
    background: transparent;
    border-color: var(--color-gray-100);
}

@media screen and (max-width: 1024px) {
    .contactWrapper{
        gap: 1.5rem;
    }
    .contactWrapper a
    {
        padding: 1rem;
        font-size: 1.2rem;
        border-radius: 1rem;
    }
}