.trainersContainer
{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
}

.card.trainer
{
    padding: 1rem;
}
.trainerImg
{
    border-radius: 0.2rem;
    overflow: hidden;
}

.trainer p
{
    margin-top: 0.5rem;
}

.trainerSocials
{
    display: flex;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}

.trainer h3
{
    margin-top: 2rem;
}

.trainerSocials a
{
    font-size: 0.7rem;
    background: var(--color-gray-600);
    padding: 0.8rem;
    border-radius: 0.7rem;
    transition: var(--transition);
}

.trainer:hover a{
    background: var(--color-gray-500);
}


/* Media Quieries  */
@media screen and (max-width: 1024px){
    .trainersContainer{
        grid-template-columns: 1fr 1fr;
        gap: 2.5rem;
    }
}

@media screen and (max-width: 600px){
    .trainersContainer{
        grid-template-columns: 1fr;
        gap: 2rem;
    }

    .card.trainer{
        width: 90%;
        margin-inline: auto;
    }
}