.aboutSectionImage
{
    border-radius: 50%;
    overflow: hidden;
    transform: skew(15deg);
    transition: var(--transition);
}

.aboutSectionImage:nth-child(2)
{
    transform: skew(-15deg);
}

.aboutSectionImage:hover {
    transform: skew(0);
}
    
.aboutSectionContent h1
{
    margin-bottom: 2rem;
}

.aboutSectionContent p
{
    margin-bottom: 1rem;
}

.aboutStoryContainer,.aboutMissionContainer
{
    display: grid;
    grid-template-columns: 40% 50%;
    gap: 10%;
}

.aboutVisionContainer
{
    display: grid;
    grid-template-columns: 50% 40%;
    gap: 10%;
}

/* Media Quiries  */
@media screen and (max-width: 1024px) 
{
    .aboutSectionImage
    {
        width: 60%;
    }

    .aboutSectionContent h1
    {
        margin-top: 1.2rem;
    }

    .aboutMissionContainer, .aboutStoryContainer ,.aboutVisionContainer
    {
        grid-template-columns: 1fr;
        gap: 3rem;
    }

    .aboutVision , .aboutSectionImage
    {
        grid-row: 1;
    }
}

@media screen and (min-width: 600px) {
    .aboutSectionImage
    {
        width: 80%;
        margin-inline: auto;
    }

    .aboutMissionContainer, .aboutStoryContainer ,.aboutVisionContainer
    {
        gap: 2rem;
    }
}
